const defaultState ={
	customers: []
}
const ADD_MANY_CUSTOMERS = 'ADD_MANY_CUSTOMERS'
const ADD_CUSTOMER = 'ADD_CUSTOMER'
const REMOVE_CUSTOMERS = 'REMOVE_CUSTOMERS'
const REMOVE_ALL_CUSTOMERS = 'REMOVE_ALL_CUSTOMERS'


export const customerReducer = (state = defaultState, action) => {
	switch(action.type) {
	  case ADD_MANY_CUSTOMERS:
		return{...state, customers: [...state.customers, ...action.payload]}
	  case ADD_CUSTOMER:
		return {...state, customers: [...state.customers, action.payload]}
	  case REMOVE_CUSTOMERS:
		return {...state, customers: [...state.customers.filter(customer => customer.id !== action.payload), action.payload]}
	  case REMOVE_ALL_CUSTOMERS:
		return {...state, customers: []}
	  default:
		return state
	}
  }
 
export const addCustomerAction = (payload) => ({type: ADD_CUSTOMER, payload})  
export const removeCustomerAction = (payload) => ({type: REMOVE_CUSTOMERS, payload})  
export const addManyCustomersAction=(payload) => ({type: ADD_MANY_CUSTOMERS, payload})
export const removeAllCustomersAction=(payload) => ({type: REMOVE_ALL_CUSTOMERS, payload})