import { Grid } from "@material-ui/core";

const ListItems = ({items , searchCountry, onCountryClick, sorting, classes}) => {

	return (	
		<Grid container className={classes.searchAreaList}>
			{items.filter((val) => {                                {/* Search by text */}
				if(searchCountry == '') {
					return val
				} else if (val.name.toLowerCase().includes(searchCountry.toLowerCase())) {
					return val
				}}).sort((a, b) => {                                {/* Sort by DESC / ASC */}
					if (sorting) {
					  return b.name.localeCompare(a.name);
					}
					return a.name.localeCompare(b.name);
				}).map(item => {									{/* Displaying all items */}
					
				return(	
					<Grid className={classes.itemGrid} item onClick={() => onCountryClick(item)} key={item.name}>
						<p>{item.name}</p>
					</Grid>	
				)
			})}		
		</Grid>
	);
};

export default ListItems;