import './App.css';
import React, { useEffect, useState } from "react";
import axios from "axios";
import ListItems from "./components/ListItems";
import {useDispatch, useSelector} from "react-redux";
import { addCustomerAction, removeAllCustomersAction, removeCustomerAction } from './store/customerReducer';
import { fetchCustomers } from './asyncActions/customers';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import { Box, Container, TextField, Typography } from '@material-ui/core';

const useStyles = makeStyles(() => ({

  itemGrid:{
    padding: '7px 20px',
    margin:'15px',
    minWidth: '215px',
    backgroundColor: '#FFFFFF',
    boxShadow:'0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2)',
    borderRadius: '4px',

    "&:hover": {
      fontSize: '20px',
      padding: '0px 20px'
    },
  },
  searchAreaList:{
    margin: '0 auto',
    textAlign: 'center',
    padding: '0 30px',
    justifyContent: 'space-between'
  },
  infoAreaDate:{
    textDecoration: 'underline',
    minWidth: '85px',
  },
  infoAreaName:{
    fontWeight: '500'
  },
  infoAreaHoliday:{
    display: 'flex',
    margin: '15px auto',
    gap: '20px',
    paddingLeft: '25%'
  },
  searchArea: {
    marginLeft: '20px',
    display: 'flex'
  },
  customer:{
    padding: '5px 20px',
    color: '#323232',
  },
  customersBox: {
    marginBottom: '20px'
  },
  searchAreaBox: {
    display: 'flex',
    maxHeight:'40px',
    marginTop: '10px',
    marginBottom: '10px'
  },
  searchAreaTitle: {
    marginTop: ' 10px',
    marginLeft: '30px'
  },
  currentCountryTitle:{
    textAlign: 'center',
    margin: '20px 0',
    color: '#323232'
  },
  searchField: {
    display: 'flex',
    alignItems: 'center',
    gap: '10px',
    marginLeft: '20px',
  },
  mainSearchArea: {
    margin: '30px',
    display: 'block'
  },
  infoArea: {
    width: '43%',
    opacity: '1',
    position: 'fixed',
    top: '5%',
    left: '35%',
    maxWidth: '32%',
    padding: '20px 0',
    backgroundColor: '#FFFFFF',
    boxShadow: '0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2)',
    borderRadius: '4px'
  }
}))

const App = () => { 
  const [data, setData] = useState([]);
  const [searchCountry ,setSearchCountry] = useState('')
  const [selectedCountryHolidays, setselectedCountryHolidays] = useState([]);
	const [sorting, setSorting] = useState(true);
  const [text, setText] = useState('')
  const [currentCountry, setCurrentCountry] = useState('')

  const classes = useStyles();

  useEffect(() => {
    axios
      .get('https://date.nager.at/api/v3/AvailableCountries')
      .then((response) => {
        setData(response.data)
      })
      .catch(error => console.log("Axios error: ", error));
  }, []);

  const onCountryClick = (item) => {
    setCurrentCountry(item.name)
    axios
      .get(`https://date.nager.at/api/v3/NextPublicHolidays/${item.countryCode}`)
      .then((response) => {
        setselectedCountryHolidays(response.data)
      })
      .catch(error => console.log("Axios error: ", error));
  }

  const onInputChange = (text) => {
    setSearchCountry(text.target.value);
    setselectedCountryHolidays([]);
    setText(text.target.value)
  }

  const dispatch = useDispatch()
  const customers = useSelector(state => state.customers.customers)
 
  const addCustomer = (name) => {
    const customer = {
      name,
      id: Date.now(),
    }
    dispatch(addCustomerAction(customer))
  }

  const removeCustomer = (customer) => {
    dispatch(removeCustomerAction(customer.id))
  }

  const removeAllCustomers = (customers) => {
    dispatch(removeAllCustomersAction(customers))
  }

  return (
    <Container className="container">
      <Box className="body">
        
        <Box className={classes.mainSearchArea}>
        <div style={{display:'flex'}}>
          <Box className={classes.searchAreaBox}>
            <Button variant='contained'color="primary" onClick={() => addCustomer(prompt())}>Add customer</Button>
            <Button className={classes.searchArea} variant='contained' onClick={() => dispatch(fetchCustomers())}>Receive customers from database</Button>
            <Button className={classes.searchArea} variant='contained'  onClick={() => removeAllCustomers()}>Remove customers from database</Button>
          </Box>
          <Box className={classes.searchField} >
            <TextField style={{paddingBottom: '10px'}} label="Search country" variant="standard"  type="text" onChange={onInputChange} value={text}/>

            <Button variant='contained' color='primary'
              className="search-field__button"
              onClick={() => {setSorting(!sorting)}}> {sorting ? 'ASC' : 'DESC'}
            </Button>

            <Button variant='contained' color='primary'
              onClick={() => {
              setSorting(true);
              setSearchCountry('');
              setselectedCountryHolidays([])
              setText('')
              setCurrentCountry('')
              }}>RESET
            </Button>
          </Box>
          </div>
          <Container className={classes.customersBox}>
            {customers.map((customer, index )=> 
              <Typography className={classes.customer} key={index} onClick={() => removeCustomer(customer) }>{customer.name}</Typography>
            )}
          </Container>
    
        

          <Box className={classes.currentCountryTitle}>
            {currentCountry ? <Typography variant='h5'>Holidays in {currentCountry}</Typography> : <Typography variant='h5'>Click on country for more info</Typography>} 
          </Box>

          <ListItems classes={classes} items={data} searchCountry={searchCountry} sorting={sorting} onCountryClick={onCountryClick} setCurrentCountry={setCurrentCountry}/>
        </Box>
     

     
        
        
        {currentCountry ? <Box className={classes.infoArea}>
          <Typography style={{textAlign: 'center'}} variant='h5'>Holidays in {currentCountry}</Typography>
          <HighlightOffIcon onClick={() => setCurrentCountry()}  style={{marginLeft: "auto", display:'block', marginRight: '15px'}}></HighlightOffIcon>

          {selectedCountryHolidays.map((e, index) => {
            return (
              <Box className={classes.infoAreaHoliday} key={index}>
                  <Typography className={classes.infoAreaDate}>{e.date}</Typography>
                  <Typography className={classes.infoAreaName}>{e.localName}</Typography>
              </Box>
          )})}

        </Box> : null}    
      </Box>
    </Container>
  );
}

export default App;
